/* eslint-disable @typescript-eslint/no-explicit-any */
<template>
  <div class="tag-list-container">
    <!--  -->
    <el-form :inline="true" :model="searchForm" class="search-form">
      <el-form-item label="">
        <el-input v-model="searchForm.keyword" placeholder="/" clearable />
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.category" placeholder="" clearable>
          <el-option label="Business" value="Business" />
          <el-option label="Warning" value="Warning" />
          <el-option label="Anomaly" value="Anomaly" />
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.status" placeholder="" clearable>
          <el-option label="" value="Active" />
          <el-option label="" value="Deprecated" />
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleSearch"></el-button>
        <el-button @click="resetSearchForm"></el-button>
      </el-form-item>
    </el-form>

    <!--  -->
    <div class="operation-bar">
      <el-button type="primary" @click="handleAddTag"></el-button>
    </div>

    <!--  -->
    <el-table :data="allTableData" v-loading="loading" border style="width: 100%">
      <el-table-column prop="tagKey" label="" min-width="120" />
      <el-table-column prop="tagValue" label="" min-width="120" />
      <el-table-column prop="category" label="" width="120">
        <template #default="{ row }">
          <el-tag :type="getCategoryTagType(row.category)">{{ row.category }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="" width="100">
        <template #default="{ row }">
          <el-tag :type="getStatusTagType(row.status)">
            {{ row.status === 'Active' ? '' : '' }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="description" label="" min-width="200" show-overflow-tooltip />
      <el-table-column prop="updatedAt" label="" width="160" />
      <el-table-column label="" width="200" fixed="right">
        <template #default="{ row }">
          <el-button link type="primary" @click="handleEditTag(row)"></el-button>
          <el-button 
            link 
            :type="row.status === 'Active' ? 'warning' : 'success'"
            @click="handleChangeStatus(row)"
          >
            {{ row.status === 'Active' ? '' : '' }}
          </el-button>
          <el-button link type="danger" @click="handleDeleteTag(row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="pagination-container">
      <el-pagination
        v-model:current-page="currentPage"
        v-model:page-size="pageSize"
        :page-sizes="[10, 20, 50, 100]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>

    <!--  -->
    <el-dialog
      v-model="tagFormDialogVisible"
      :title="tagDialogTitle"
      width="60%"
      :close-on-click-modal="false"
      @close="handleCloseTagFormDialog"
      destroy-on-close
    >
      <TagForm 
        ref="tagFormRef" 
        :initial-data="currentTag" 
        :mode="tagDialogMode" 
        @save-success="handleSaveSuccess" 
      />
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="handleCloseTagFormDialog"></el-button>
          <el-button type="primary" @click="handleSubmitTagForm"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import TagForm from './tagForm.vue'; // 
import { ElMessage, ElMessageBox } from 'element-plus'; //  Message  MessageBox

export default {
  name: 'TagList',
  components: { TagForm }, //  ( import )
  data() {
    return {
      loading: false,
      currentPage: 1,
      pageSize: 10,
      tagFormDialogVisible: false,
      tagDialogMode: 'add', // 'add' or 'edit'
      currentTag: null, // 
      tagFormRef: null, //  TagForm 
      searchForm: { keyword: '', category: '', status: '' },
      allTableData: [], // 
      total: 0, // 
    };
  },
  computed: {
    tagDialogTitle() {
      return this.tagDialogMode === 'add' ? '' : '';
    }
  },
  methods: {
    handleSearch() {
      this.currentPage = 1; // 
      //  API 
      console.log(':', this.searchForm);
      this.fetchTags(); //  API
    },
    resetSearchForm() {
      this.searchForm = { keyword: '', category: '', status: '' };
      this.currentPage = 1;
      this.fetchTags(); //  API
    },
    fetchTags() {
      this.loading = true;
      //  API 
      console.log('Fetching tags with filters:', this.searchForm);
      setTimeout(() => {
        // 
        this.allTableData = [
          {
            id: 1,
            tagKey: 'environment',
            tagValue: 'production',
            category: 'Business',
            status: 'Active',
            description: '',
            updatedAt: '2024-03-20 10:00:00'
          },
          {
            id: 2,
            tagKey: 'priority',
            tagValue: 'high',
            category: 'Warning',
            status: 'Active',
            description: '',
            updatedAt: '2024-03-20 11:00:00'
          },
          {
            id: 3,
            tagKey: 'error',
            tagValue: 'critical',
            category: 'Anomaly',
            status: 'Deprecated',
            description: '',
            updatedAt: '2024-03-20 12:00:00'
          }
        ];
        this.total = this.allTableData.length;
        this.loading = false;
        console.log('Tags fetched (simulated).');
      }, 500);
    },
    getCategoryTagType(category) {
       const map = { 'Business': 'primary', 'Warning': 'warning', 'Anomaly': 'danger' };
       return map[category] || 'info';
    },
    getStatusTagType(status) {
       return status === 'Active' ? 'success' : 'info';
    },
    handleAddTag() {
      this.tagDialogMode = 'add';
      this.currentTag = null; // 
      this.tagFormDialogVisible = true;
      console.log('');
      this.$nextTick(() => {
         //  DOM  ref
         this.$refs.tagFormRef?.resetForm(); // 
      });
    },
    handleEditTag(row) {
      this.tagDialogMode = 'edit';
      // 
      this.currentTag = JSON.parse(JSON.stringify(row));
      this.tagFormDialogVisible = true;
      console.log(', :', this.currentTag);
       // setForm  TagForm  watch initialData 
    },
    handleChangeStatus(row) {
      const newStatus = row.status === 'Active' ? 'Deprecated' : 'Active';
      const actionText = newStatus === 'Active' ? '' : '';
      ElMessageBox.confirm(`${actionText} "${row.tagKey}" ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //  then 
        const index = this.allTableData.findIndex(item => item.id === row.id);
        if (index !== -1) {
          this.allTableData[index].status = newStatus;
          this.allTableData[index].updatedAt = new Date().toLocaleString(); // 
          ElMessage.success(`${actionText}`);
          // this.fetchTags(); // 
        }
      }).catch(() => {
        ElMessage.info(`${actionText}`);
      });
    },
    handleDeleteTag(row) {
       if (row.status === 'Active') {
        ElMessage.warning('');
        return;
      }
      ElMessageBox.confirm(` "${row.tagKey}" ? !`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error'
      }).then(() => {
         //  then 
         //  API 
        console.log(`Deleting tag ${row.tagKey}`);
        //  API 
        this.allTableData = this.allTableData.filter(item => item.id !== row.id);
        ElMessage.success('');
        // this.fetchTags(); // 
      }).catch(() => {
        ElMessage.info('');
      });
    },
    handleCloseTagFormDialog() {
      this.tagFormDialogVisible = false;
      this.currentTag = null;
      console.log('');
    },
    handleSubmitTagForm() {
      console.log(' TagForm  submitForm ');
      this.$refs.tagFormRef?.submitForm();
    },
    handleSaveSuccess(savedTagData) {
        console.log('TagList  save-success , :', savedTagData);
        this.handleCloseTagFormDialog(); // 

        // 
        if (this.tagDialogMode === 'add') {
            // 
            this.allTableData.unshift(savedTagData);
        } else {
            // 
            const index = this.allTableData.findIndex(item => item.id === savedTagData.id);
            if (index !== -1) {
                this.allTableData.splice(index, 1, savedTagData);
            } else {
                // 
                this.allTableData.push(savedTagData);
            }
        }
        ElMessage.success(` ${this.tagDialogMode === 'add' ? '' : ''}!`);
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.currentPage = 1; // 
      this.fetchTags();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.fetchTags();
    }
  },
  mounted() {
    this.fetchTags(); // 
  }
};
</script>

<style scoped>
.tag-list-container {
  padding: 20px;
}

.search-form {
  margin-bottom: 20px;
}

.operation-bar {
  margin-bottom: 20px;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}
</style> 