<template>
  <div class="driver-check-in-container">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>{{ isEnglish ? 'Loading Check In Form' : '' }}</span>
          <el-switch
            v-model="isEnglish"
            active-text="English"
            inactive-text=""
            @change="handleLanguageChange"
          />
        </div>
      </template>
      <div v-if="!showDetails">
        <div class="form-description">
          <p>{{ isEnglish ? 'Please provide the following information to inquire about the loading information you need. The system will display delivery details after query:' : '' }}</p>
          <ol>
            <li>Check-In time</li>
            <li>Phone number</li>
            <li>Driver's license number</li>
            <li>Trailer#</li>
            <li>Deadline</li>
          </ol>
        </div>
        
        <div class="demo-info">
          <el-alert
            :title="isEnglish ? 'Demo data available with Pick Up #: 12345, 23456, 34567' : ': 12345, 23456, 34567'"
            type="info"
            :closable="false"
            show-icon
          />
        </div>
        
        <el-form :model="form" label-width="140px" class="check-in-form">
          <el-form-item :label="isEnglish ? 'Pick Up #' : '(Pick Up #)'">
            <el-input v-model="form.pickupNumber" :placeholder="isEnglish ? 'Enter pick up number' : ''">
              <template #prepend>
                <el-icon><Document /></el-icon>
              </template>
              <template #append>
                <el-tooltip :content="isEnglish ? 'Click search button after input' : ''" placement="top">
                  <el-icon><QuestionFilled /></el-icon>
                </el-tooltip>
              </template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit" :loading="loading" size="large">
              <el-icon><Search /></el-icon> {{ isEnglish ? 'Search' : '' }}
            </el-button>
            <el-button @click="resetForm" size="large">
              <el-icon><RefreshRight /></el-icon> {{ isEnglish ? 'Reset' : '' }}
            </el-button>
          </el-form-item>
        </el-form>
      </div>
      <div v-else>
        <div class="details-header">
          <h3>{{ isEnglish ? 'Pick Up Information' : '' }}</h3>
          <el-tag type="success" size="large" effect="dark">{{ getStatusText(deliveryInfo.status) }}</el-tag>
        </div>
        
        <el-form :model="deliveryForm" label-width="140px" class="delivery-form">
          <el-descriptions :column="1" border class="delivery-info" size="large">
            <el-descriptions-item :label="isEnglish ? 'Pick Up #' : ' (Pick Up #)'">{{ deliveryInfo.pickupNumber }}</el-descriptions-item>
            <el-descriptions-item :label="isEnglish ? 'Train Number' : ''">{{ deliveryInfo.trainNumber }}</el-descriptions-item>
            
            <!--  -->
            <el-descriptions-item :label="isEnglish ? 'Driver Phone' : ''">
              <el-input v-model="deliveryForm.driverPhone" size="small" :placeholder="isEnglish ? 'Enter driver phone' : ''" />
            </el-descriptions-item>
            
            <el-descriptions-item :label="isEnglish ? 'Driver License' : ''">
              <el-input v-model="deliveryForm.driverLicense" size="small" :placeholder="isEnglish ? 'Enter driver license' : ''" />
            </el-descriptions-item>
            
            <el-descriptions-item :label="isEnglish ? 'Trailer #' : ' (Trailer #)'">
              <el-input v-model="deliveryForm.carNumber" size="small" :placeholder="isEnglish ? 'Enter trailer number' : ''" />
            </el-descriptions-item>
            
            <el-descriptions-item :label="isEnglish ? 'Pick Up Appointment Time' : ''">{{ deliveryInfo.appointmentTime }}</el-descriptions-item>
          </el-descriptions>
        </el-form>
        
        <!--  -->
        <div class="delivery-details">
          <h4>{{ isEnglish ? 'Delivery Details' : '' }}</h4>
          <el-table 
            :data="deliveryDetails" 
            style="width: 100%" 
            border 
            stripe
            header-row-class-name="table-header"
            :max-height="400">
            <el-table-column prop="pcNumber" :label="isEnglish ? 'PC #' : 'PC'" width="120" sortable />
            <el-table-column prop="appointmentParty" :label="isEnglish ? 'Appointment Party' : ''" width="100" />
            <el-table-column prop="appointmentTime" :label="isEnglish ? 'Appointment Time' : ''" width="150" sortable />
            <el-table-column prop="startWarehouse" :label="isEnglish ? 'Start Warehouse' : ''" width="120" />
            <el-table-column prop="destination" :label="isEnglish ? 'Destination' : ''" min-width="150" />
            <el-table-column prop="palletCount" :label="isEnglish ? 'Pallets' : ''" width="80" sortable align="center" />
            <el-table-column prop="weight" :label="isEnglish ? 'Weight' : ''" width="100" sortable align="center" />
            <el-table-column prop="loadingType" :label="isEnglish ? 'Loading Type' : ''" width="100" />
            <el-table-column prop="boxCount" :label="isEnglish ? 'Boxes' : ''" width="80" sortable align="center" />
            <el-table-column prop="priority" :label="isEnglish ? 'Priority' : ''" width="80" sortable align="center" />
            <el-table-column prop="cubicMeter" :label="isEnglish ? 'Cubic Meter' : ''" width="80" sortable align="center" />
          </el-table>
        </div>
        
        <div class="action-buttons">
          <el-button @click="goBack" size="large">
            <el-icon><Back /></el-icon> {{ isEnglish ? 'Back' : '' }}
          </el-button>
          <el-button type="success" @click="handleCheckIn" size="large" :loading="checkingIn" :disabled="deliveryInfo.status === 'completed'">
            <el-icon><Check /></el-icon> {{ isEnglish ? 'Check In' : '' }}
          </el-button>
        </div>
        
        <!-- Check In  -->
        <el-dialog
          v-model="checkInDialogVisible"
          :title="isEnglish ? 'Confirm Check In' : ''"
          width="30%"
        >
          <span>{{ isEnglish ? 'Are you sure to check in this delivery?' : '' }}</span>
          <template #footer>
            <span class="dialog-footer">
              <el-button @click="checkInDialogVisible = false">{{ isEnglish ? 'Cancel' : '' }}</el-button>
              <el-button type="primary" @click="confirmCheckIn" :loading="checkingIn">
                {{ isEnglish ? 'Confirm' : '' }}
              </el-button>
            </span>
          </template>
        </el-dialog>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive, watch } from 'vue';
import { ElMessage } from 'element-plus';
import { Search, Document, QuestionFilled, RefreshRight, Back, Check } from '@element-plus/icons-vue';

const form = reactive({
  pickupNumber: '',
});

const deliveryForm = reactive({
  driverPhone: '',
  driverLicense: '',
  carNumber: '',
});

const loading = ref(false);
const checkingIn = ref(false);
const checkInDialogVisible = ref(false);
const showDetails = ref(false);
const deliveryInfo = ref(null);
const deliveryDetails = ref([]);
const isEnglish = ref(false);

// 
const mockDeliveryData = [
  {
    pickupNumber: '12345',
    trainNumber: 'T6789',
    driverPhone: '13800138000',
    driverLicense: 'L12345678',
    carNumber: 'TR987',
    appointmentTime: '2024-07-30 14:00',
    status: 'pending', // pending, in-progress, completed
    details: [
      {
        pcNumber: 'PC2024030100',
        appointmentParty: 'A',
        appointmentTime: '2024-02-29 14:00:00',
        startWarehouse: '',
        destination: '',
        palletCount: 10,
        weight: 1000,
        loadingType: '',
        boxCount: 100,
        priority: 1,
        cubicMeter: 20
      },
      {
        pcNumber: 'PC2024030100',
        appointmentParty: 'B',
        appointmentTime: '2024-02-29 15:00:00',
        startWarehouse: '',
        destination: '',
        palletCount: 10,
        weight: 1000,
        loadingType: '',
        boxCount: 500,
        priority: 2,
        cubicMeter: 20
      }
    ]
  },
  {
    pickupNumber: '23456',
    trainNumber: 'T7890',
    driverPhone: '13900139000',
    driverLicense: 'L87654321',
    carNumber: 'TR654',
    appointmentTime: '2024-08-02 10:30',
    status: 'in-progress',
    details: [
      {
        pcNumber: 'PC2024030200',
        appointmentParty: 'C',
        appointmentTime: '2024-03-05 09:00:00',
        startWarehouse: '',
        destination: '',
        palletCount: 15,
        weight: 1500,
        loadingType: '',
        boxCount: 150,
        priority: 1,
        cubicMeter: 25
      }
    ]
  },
  {
    pickupNumber: '34567',
    trainNumber: 'T8901',
    driverPhone: '13700137000',
    driverLicense: 'L13579246',
    carNumber: 'TR321',
    appointmentTime: '2024-08-05 09:15',
    status: 'completed',
    details: [
      {
        pcNumber: 'PC2024030300',
        appointmentParty: 'D',
        appointmentTime: '2024-03-10 13:30:00',
        startWarehouse: '',
        destination: '',
        palletCount: 8,
        weight: 800,
        loadingType: '',
        boxCount: 80,
        priority: 3,
        cubicMeter: 15
      },
      {
        pcNumber: 'PC2024030301',
        appointmentParty: 'E',
        appointmentTime: '2024-03-10 14:30:00',
        startWarehouse: '',
        destination: '',
        palletCount: 12,
        weight: 1200,
        loadingType: '',
        boxCount: 120,
        priority: 2,
        cubicMeter: 22
      }
    ]
  }
];

//  deliveryInfo 
watch(() => deliveryInfo.value, (newVal) => {
  if (newVal) {
    deliveryForm.driverPhone = newVal.driverPhone || '';
    deliveryForm.driverLicense = newVal.driverLicense || '';
    deliveryForm.carNumber = newVal.carNumber || '';
  }
}, { immediate: true });

// 
const getStatusText = (status) => {
  if (isEnglish.value) {
    switch(status) {
      case 'pending': return 'Pending';
      case 'in-progress': return 'In Progress';
      case 'completed': return 'Completed';
      default: return 'Unknown';
    }
  } else {
    switch(status) {
      case 'pending': return '';
      case 'in-progress': return '';
      case 'completed': return '';
      default: return '';
    }
  }
};

// 
const handleLanguageChange = (val) => {
  isEnglish.value = val;
};

//  API 
const fetchDeliveryInfo = async (pickupNumber) => {
  console.log(` ${pickupNumber} `);
  await new Promise(resolve => setTimeout(resolve, 800)); // 
  
  const data = mockDeliveryData.find(item => item.pickupNumber === pickupNumber);
  return data || null;
};

const onSubmit = async () => {
  if (!form.pickupNumber) {
    ElMessage.warning(isEnglish.value ? 'Please enter pick up number' : '');
    return;
  }
  
  loading.value = true;
  try {
    const data = await fetchDeliveryInfo(form.pickupNumber);
    if (data) {
      deliveryInfo.value = data;
      deliveryDetails.value = data.details || [];
      showDetails.value = true;
    } else {
      ElMessage.error(isEnglish.value ? 'No information found for this pick up number' : '');
    }
  } catch (error) {
    console.error(':', error);
    ElMessage.error(isEnglish.value ? 'Query failed, please try again later' : '');
  } finally {
    loading.value = false;
  }
};

const resetForm = () => {
  form.pickupNumber = '';
};

const handleCheckIn = () => {
  // 
  if (!deliveryForm.driverPhone || !deliveryForm.driverLicense || !deliveryForm.carNumber) {
    ElMessage.warning(isEnglish.value ? 'Please fill in all required fields' : '');
    return;
  }
  checkInDialogVisible.value = true;
};

const confirmCheckIn = async () => {
  checkingIn.value = true;
  try {
    // API
    await new Promise(resolve => setTimeout(resolve, 1000));
    
    // 
    if (deliveryInfo.value) {
      // deliveryInfo
      deliveryInfo.value.driverPhone = deliveryForm.driverPhone;
      deliveryInfo.value.driverLicense = deliveryForm.driverLicense;
      deliveryInfo.value.carNumber = deliveryForm.carNumber;
      deliveryInfo.value.status = 'completed';
      
      // 
      const index = mockDeliveryData.findIndex(item => item.pickupNumber === deliveryInfo.value.pickupNumber);
      if (index !== -1) {
        mockDeliveryData[index] = { ...deliveryInfo.value };
      }
    }
    
    ElMessage.success(isEnglish.value ? 'Check in successfully' : '');
    checkInDialogVisible.value = false;
  } catch (error) {
    console.error(':', error);
    ElMessage.error(isEnglish.value ? 'Check in failed, please try again later' : '');
  } finally {
    checkingIn.value = false;
  }
};

const goBack = () => {
  showDetails.value = false;
  form.pickupNumber = ''; // 
  deliveryInfo.value = null;
  deliveryDetails.value = [];
};
</script>

<style scoped>
.driver-check-in-container {
  padding: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 18px;
}

.box-card {
  width: 98%;
  max-width: 1200px;
  margin: 20px auto;
  border-radius: 8px;
}

.form-description {
  margin-bottom: 20px;
  color: #606266;
}

.check-in-form {
  margin-top: 30px;
}

.demo-info {
  margin: 20px 0;
}

ol {
  padding-left: 20px;
  margin-bottom: 20px;
}

li {
  margin-bottom: 5px;
}

.details-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.details-header h3 {
  margin: 0;
  font-size: 20px;
  color: #303133;
}

.delivery-info {
  margin-bottom: 30px;
}

.delivery-form :deep(.el-descriptions__body) {
  width: 100%;
}

.delivery-form :deep(.el-descriptions__label) {
  width: 140px;
  font-weight: bold;
}

.delivery-form :deep(.el-input) {
  width: 100%;
}

.delivery-details {
  margin-top: 30px;
}

.delivery-details h4 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #303133;
  font-size: 18px;
}

.action-buttons {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 15px;
}

:deep(.table-header) {
  background-color: #f5f7fa;
  color: #303133;
  font-weight: bold;
}

:deep(.el-switch__label) {
  color: #606266;
}

:deep(.el-switch__label.is-active) {
  color: #409EFF;
}
</style> 