<template>
  <div class="appointment-management">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span></span>
          <div class="header-buttons">
            <el-button type="primary" size="small" @click="handleAdd"></el-button>
            <el-button type="info" size="small" @click="showLogs"></el-button>
          </div>
        </div>
      </template>

      <!--  -->
      <div class="search-area">
        <el-form :inline="true" :model="searchForm" class="demo-form-inline">
          <el-form-item label="">
            <el-select v-model="searchForm.platform" placeholder="" style="width: 200px;">
              <el-option label="" value=""></el-option>
              <el-option label="Amazon" value="Amazon"></el-option>
              <el-option label="Walmart" value="Walmart"></el-option>
              <el-option label="eBay" value="eBay"></el-option>
              <el-option label="Shopify" value="Shopify"></el-option>
              <!--  -->
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="searchForm.warehouseCode" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handleSearch"></el-button>
            <el-button @click="resetForm"></el-button>
          </el-form-item>
        </el-form>
      </div>

      <!--  -->
      <div class="view-management">
        <span class="view-label"></span>
        <el-radio-group v-model="currentView" size="small" class="status-view-group">
          <el-radio-button value="all">({{ total }})</el-radio-button>
          <el-radio-button value="unused">({{ getStatusCount('unused') }})</el-radio-button>
          <el-radio-button value="used">({{ getStatusCount('used') }})</el-radio-button>
          <el-radio-button value="amazon_deleted">({{ getStatusCount('amazon_deleted') }})</el-radio-button>
          <el-radio-button value="deleted">({{ getStatusCount('deleted') }})</el-radio-button>
          <!--  -->
          <el-radio-button 
            v-for="view in customViews" 
            :key="view.id" 
            :label="view.id"
            @contextmenu.prevent="handleContextMenu($event, view)"
          >
            {{view.name}}
          </el-radio-button>
        </el-radio-group>
        
        <el-button type="text" size="small" @click="handleSaveView"></el-button>
        <el-button type="text" size="small" @click="showGroupModal"></el-button>
        <el-button type="text" size="small" @click="showSortModal"></el-button>
        <el-button type="text" size="small" @click="showFilterPanel"></el-button>
        <el-button type="text" size="small" @click="showColumnSettings"></el-button>
      </div>

      <!--  -->
      <groupable-table
        ref="mainTable"
        :data="displayData"
        row-key="id"
        :group-fields="groupField ? [groupField] : []"
        :show-selection="true"
        :show-group-column="!!groupField"
        :group-column-label="groupField ? getGroupColumnLabel(groupField) : ''"
        :default-expand-all="false"
        :show-pagination="true"
        :current-page="currentPage"
        :page-size="pageSize"
        :page-sizes="[10, 20, 50, 100]"
        :total="total"
        v-loading="loading"
        @selection-change="handleSelectionChange"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        @cell-click="handleCellClick"
        @cell-dblclick="handleCellDbClick"
        style="width: 100%"
      >
        <!--  -->
        <el-table-column prop="platform" label="" width="100">
          <template #default="scope">
            <el-tag :type="getPlatformType(scope.row.platform)">
              {{ scope.row.platform }}
            </el-tag>
          </template>
        </el-table-column>
        
        <!--  -->
        <el-table-column prop="warehouseCode" label="" width="120">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'warehouseCode')">
              <el-input
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'warehouseCode'"
                v-model="editingCell.value"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'warehouseCode')"
                @keyup.enter="handleCellEditConfirm(scope.row, 'warehouseCode')"
                v-focus
              />
              <span v-else>{{ scope.row.warehouseCode }}</span>
            </div>
          </template>
        </el-table-column>
        
        <!--  -->
        <el-table-column prop="account" label="" width="120">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'account')">
              <el-input
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'account'"
                v-model="editingCell.value"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'account')"
                @keyup.enter="handleCellEditConfirm(scope.row, 'account')"
                v-focus
              />
              <span v-else>{{ scope.row.account }}</span>
            </div>
          </template>
        </el-table-column>
        
        <!--  -->
        <el-table-column prop="appointmentId" label="" width="150">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'appointmentId')">
              <el-input
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'appointmentId'"
                v-model="editingCell.value"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'appointmentId')"
                @keyup.enter="handleCellEditConfirm(scope.row, 'appointmentId')"
                v-focus
              />
              <span v-else>{{ scope.row.appointmentId }}</span>
            </div>
          </template>
        </el-table-column>
        
        <!-- PC -->
        <el-table-column prop="pcNumber" label="PC" width="120">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'pcNumber')">
              <el-input
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'pcNumber'"
                v-model="editingCell.value"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'pcNumber')"
                @keyup.enter="handleCellEditConfirm(scope.row, 'pcNumber')"
                v-focus
              />
              <span v-else>{{ scope.row.pcNumber }}</span>
            </div>
          </template>
        </el-table-column>
        
        <!-- CRDD -->
        <el-table-column prop="crdd" label="CRDD" width="160">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'crdd')">
              <el-date-picker
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'crdd'"
                v-model="editingCell.value"
                type="datetime"
                size="small"
                value-format="YYYY-MM-DD HH:mm:ss"
                @blur="handleCellEditConfirm(scope.row, 'crdd')"
                v-focus
              />
              <span v-else>{{ scope.row.crdd }}</span>
            </div>
          </template>
        </el-table-column>
        
        <!--  -->
        <el-table-column prop="appointmentType" label="" width="120">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'appointmentType')">
              <el-select
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'appointmentType'"
                v-model="editingCell.value"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'appointmentType')"
                @change="handleCellEditConfirm(scope.row, 'appointmentType')"
                v-focus
              >
                <el-option label="" value="floor"></el-option>
                <el-option label="" value="pallet"></el-option>
              </el-select>
              <span v-else>{{ scope.row.appointmentType === 'floor' ? '' : '' }}</span>
            </div>
          </template>
        </el-table-column>
        
        <!--  -->
        <el-table-column prop="appointmentTime" label="" width="160">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'appointmentTime')">
              <el-date-picker
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'appointmentTime'"
                v-model="editingCell.value"
                type="datetime"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'appointmentTime')"
                v-focus
              />
              <span v-else>{{ scope.row.appointmentTime }}</span>
            </div>
          </template>
        </el-table-column>
        
        <!--  -->
        <el-table-column prop="earliestAppointmentTime" label="" width="160">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'earliestAppointmentTime')">
              <el-date-picker
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'earliestAppointmentTime'"
                v-model="editingCell.value"
                type="datetime"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'earliestAppointmentTime')"
                v-focus
              />
              <span v-else>{{ scope.row.earliestAppointmentTime }}</span>
            </div>
          </template>
        </el-table-column>
        
        <!--  -->
        <el-table-column prop="status" label="" width="120">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'status')">
              <el-select
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'status'"
                v-model="editingCell.value"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'status')"
                @change="handleCellEditConfirm(scope.row, 'status')"
                v-focus
              >
                <el-option label="" value="unused"></el-option>
                <el-option label="" value="used"></el-option>
                <el-option label="" value="amazon_deleted"></el-option>
                <el-option label="" value="deleted"></el-option>
              </el-select>
              <el-tag v-else :type="getStatusType(scope.row.status)">
                {{ getStatusText(scope.row.status) }}
              </el-tag>
            </div>
          </template>
        </el-table-column>
        
        <!--  -->
        <el-table-column prop="abnormalStatus" label="" width="120">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'abnormalStatus')">
              <el-select
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'abnormalStatus'"
                v-model="editingCell.value"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'abnormalStatus')"
                @change="handleCellEditConfirm(scope.row, 'abnormalStatus')"
                v-focus
              >
                <el-option label="" value="normal"></el-option>
                <el-option label="NCNS" value="ncns"></el-option>
                <el-option label="" value="sold"></el-option>
                <el-option label="" value="amazon_deleted"></el-option>
                <el-option label="" value="other"></el-option>
              </el-select>
              <el-tag v-else :type="getAbnormalStatusType(scope.row.abnormalStatus)">
                {{ getAbnormalStatusText(scope.row.abnormalStatus) }}
              </el-tag>
            </div>
          </template>
        </el-table-column>
        
        <!--  -->
        <el-table-column prop="followUpRecord" label="" min-width="150">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'followUpRecord')">
              <el-input
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'followUpRecord'"
                v-model="editingCell.value"
                type="textarea"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'followUpRecord')"
                @keyup.enter="handleCellEditConfirm(scope.row, 'followUpRecord')"
                v-focus
              />
              <span v-else>{{ scope.row.followUpRecord }}</span>
            </div>
          </template>
        </el-table-column>
        
        <!-- Memo -->
        <el-table-column prop="memo" label="Memo" min-width="150">
          <template #default="scope">
            <div v-if="!scope.row.isGroup" class="editable-cell" @dblclick="handleCellDbClick(scope.row, 'memo')">
              <el-input
                v-if="editingCell.rowId === scope.row.id && editingCell.prop === 'memo'"
                v-model="editingCell.value"
                type="textarea"
                size="small"
                @blur="handleCellEditConfirm(scope.row, 'memo')"
                @keyup.enter="handleCellEditConfirm(scope.row, 'memo')"
                v-focus
              />
              <span v-else>{{ scope.row.memo }}</span>
            </div>
          </template>
        </el-table-column>
        
        <!--  -->
        <el-table-column label="" width="180" fixed="right">
          <template #default="scope">
            <el-button size="small" @click="handleDetail(scope.row)"></el-button>
            <el-dropdown @command="handleCommand" style="margin-left: 10px;">
              <el-button size="small">
                <i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item :command="{action: 'edit', row: scope.row}"></el-dropdown-item>
                  <el-dropdown-item :command="{action: 'followUp', row: scope.row}"></el-dropdown-item>
                  <el-dropdown-item :command="{action: 'delete', row: scope.row}"></el-dropdown-item>
                  <el-dropdown-item :command="{action: 'createPC', row: scope.row}">PC</el-dropdown-item>
                  <el-dropdown-item :command="{action: 'statistics', row: scope.row}"></el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
          </template>
        </el-table-column>
      </groupable-table>

      <!-- groupable-table -->
    </el-card>

    <!-- / -->
    <el-dialog
      v-model="formDialogVisible"
      :title="dialogTitle"
      width="60%"
      :before-close="handleCloseDialog"
      destroy-on-close
    >
      <el-form :model="currentAppointment" ref="appointmentFormRef" label-width="120px">
        <el-form-item label="" prop="platform" :rules="{ required: true, message: '', trigger: 'change' }">
          <el-select v-model="currentAppointment.platform" placeholder="">
            <el-option label="Amazon" value="Amazon"></el-option>
            <el-option label="Walmart" value="Walmart"></el-option>
            <el-option label="eBay" value="eBay"></el-option>
            <el-option label="Shopify" value="Shopify"></el-option>
             <!--  -->
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="warehouseCode" :rules="{ required: true, message: '', trigger: 'blur' }">
          <el-input v-model="currentAppointment.warehouseCode" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="account">
          <el-input v-model="currentAppointment.account" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="appointmentId" :rules="{ required: true, message: '', trigger: 'blur' }">
          <el-input v-model="currentAppointment.appointmentId" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="PC" prop="pcNumber">
          <el-input v-model="currentAppointment.pcNumber" placeholder="PC"></el-input>
        </el-form-item>
        <el-form-item label="CRDD" prop="crdd">
          <el-date-picker
            v-model="currentAppointment.crdd"
            type="datetime"
            placeholder="CRDD"
            value-format="YYYY-MM-DD HH:mm:ss"
          />
        </el-form-item>
        <el-form-item label="" prop="appointmentType">
          <el-select v-model="currentAppointment.appointmentType" placeholder="">
            <el-option label="" value="floor"></el-option>
            <el-option label="" value="pallet"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="appointmentTime">
          <el-date-picker
            v-model="currentAppointment.appointmentTime"
            type="datetime"
            placeholder=""
            value-format="YYYY-MM-DD HH:mm:ss"
          />
        </el-form-item>
        <el-form-item label="" prop="earliestAppointmentTime">
           <el-date-picker
            v-model="currentAppointment.earliestAppointmentTime"
            type="datetime"
            placeholder=""
            value-format="YYYY-MM-DD HH:mm:ss"
          />
        </el-form-item>
        <el-form-item label="" prop="status" :rules="{ required: true, message: '', trigger: 'change' }">
          <el-select v-model="currentAppointment.status" placeholder="">
            <el-option label="" value="unused"></el-option>
            <el-option label="" value="used"></el-option>
            <el-option label="" value="amazon_deleted"></el-option>
            <el-option label="" value="deleted"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="abnormalStatus">
          <el-select v-model="currentAppointment.abnormalStatus" placeholder="">
            <el-option label="" value="normal"></el-option>
            <el-option label="NCNS" value="ncns"></el-option>
            <el-option label="" value="sold"></el-option>
            <el-option label="" value="amazon_deleted"></el-option>
            <el-option label="" value="other"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="followUpRecord">
          <el-input type="textarea" v-model="currentAppointment.followUpRecord" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="Memo" prop="memo">
          <el-input type="textarea" v-model="currentAppointment.memo" placeholder="Memo"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="handleCloseDialog"></el-button>
          <el-button type="primary" @click="handleSubmitForm"></el-button>
        </span>
      </template>
    </el-dialog>

     <!-- TODO:  -->
     <!-- TODO:  -->
     <!-- TODO:  -->

    <!--  -->
    <filter-panel
      v-model:visible="filterPanelVisible"
      :columns="tableColumns"
      :existing-filters="activeFilters"
      :existing-logic="filterLogic"
      @apply-filters="applyFilters"
      @cancel="cancelFilters"
    />

    <!--  -->
    <el-dialog
      title=""
      v-model="saveViewDialogVisible"
      width="500px"
    >
      <el-form :model="viewForm" label-width="100px">
        <el-form-item label="" required>
          <el-input v-model="viewForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-checkbox-group v-model="viewForm.columns">
            <el-checkbox 
              v-for="col in tableColumns" 
              :key="col.prop" 
              :label="col.prop"
            >
              {{col.label}}
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="saveViewDialogVisible = false"></el-button>
        <el-button type="primary" @click="saveView"></el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      v-model="viewOptionsDialogVisible"
      width="300px"
    >
      <div class="view-options-menu">
        <div class="view-option-item" @click="handleRenameView">
          <i class="el-icon-edit"></i>
          <span></span>
        </div>
        <div class="view-option-item" @click="handleCopyView">
          <i class="el-icon-document-copy"></i>
          <span></span>
        </div>
        <div class="view-option-item" @click="handleDeleteView">
          <i class="el-icon-delete"></i>
          <span></span>
        </div>
      </div>
    </el-dialog>
    
    <!--  -->
    <el-dialog
      title=""
      v-model="renameViewDialogVisible"
      width="500px"
    >
      <el-form :model="renameForm" label-width="100px">
        <el-form-item label="" required>
          <el-input v-model="renameForm.name" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="renameViewDialogVisible = false"></el-button>
        <el-button type="primary" @click="saveRenameView"></el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      v-model="groupModalVisible"
      width="500px"
    >
      <div class="group-settings">
        <div class="group-level">
          <span class="group-level-label"></span>
          <el-select v-model="groupSettings.level1" placeholder="" style="width: 280px;" clearable>
            <el-option v-for="field in groupableFields" :key="field.prop" :label="field.label" :value="field.prop"></el-option>
          </el-select>
        </div>
        <div class="group-level">
          <span class="group-level-label"></span>
          <el-select v-model="groupSettings.level2" placeholder="" style="width: 280px;" clearable>
            <el-option v-for="field in groupableFields" :key="field.prop" :label="field.label" :value="field.prop"></el-option>
          </el-select>
        </div>
        <div class="group-level">
          <span class="group-level-label"></span>
          <el-select v-model="groupSettings.level3" placeholder="" style="width: 280px;" clearable>
            <el-option v-for="field in groupableFields" :key="field.prop" :label="field.label" :value="field.prop"></el-option>
          </el-select>
        </div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="groupModalVisible = false"></el-button>
          <el-button type="danger" @click="clearGroupSettings"></el-button>
          <el-button type="primary" @click="applyGroupSettings"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      v-model="sortModalVisible"
      width="500px"
    >
      <div class="sort-settings">
        <div v-for="(sort, index) in sortSettings" :key="index" class="sort-item">
          <el-select v-model="sort.field" placeholder="" style="width: 200px;" clearable>
            <el-option 
              v-for="field in sortableFields" 
              :key="field.prop" 
              :label="field.label" 
              :value="field.prop"
            ></el-option>
          </el-select>
          <el-select v-model="sort.direction" placeholder="" style="width: 120px;">
            <el-option label="" value="asc"></el-option>
            <el-option label="" value="desc"></el-option>
          </el-select>
          <el-button 
            type="danger" 
            size="small"
            @click="removeSortField(index)"
          ></el-button>
        </div>
        <div class="sort-actions">
          <el-button type="primary" plain @click="addSortField"></el-button>
        </div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="sortModalVisible = false"></el-button>
          <el-button type="danger" @click="clearSortSettings"></el-button>
          <el-button type="primary" @click="applySortSettings"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      v-model="columnSettingsVisible"
      width="600px"
    >
      <div class="column-settings">
        <div class="column-settings-header">
          <el-checkbox
            v-model="allColumnsSelected"
            @change="handleSelectAllColumns"
          ></el-checkbox>
          <el-button type="primary" size="small" @click="resetColumnSettings"></el-button>
        </div>
        <div class="column-list">
          <div v-for="item in columnSettingsList" :key="item.prop" class="column-item">
            <span class="drag-handle"></span>
            <el-checkbox v-model="item.visible">{{ item.label }}</el-checkbox>
          </div>
        </div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="columnSettingsVisible = false"></el-button>
          <el-button type="primary" @click="applyColumnSettings"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <div 
      v-show="contextMenuVisible" 
      class="context-menu" 
      :style="{left: contextMenuX + 'px', top: contextMenuY + 'px'}"
    >
      <div class="context-menu-item" @click="handleCopyView">
        <span></span>
      </div>
      <div class="context-menu-item" @click="handleRenameView">
        <span></span>
      </div>
      <div class="context-menu-item" @click="handleDeleteView">
        <span></span>
      </div>
    </div>
  </div>
</template>

<script>
import { ElMessage, ElMessageBox } from 'element-plus';

import GroupableTable from '@/components/logistics/GroupableTable.vue';
import FilterPanel from '@/components/logistics/FilterPanel.vue';

export default {
  name: 'AppointmentManagement',
  components: { 
    GroupableTable,
    FilterPanel
  },
  directives: {
    focus: {
      mounted(el) {
        el.focus();
      }
    }
  },
  data() {
    return {
      searchForm: {
        platform: '',
        warehouseCode: ''
      },
      // 
      tableData: [
        // 
        {
          id: '1', platform: 'Amazon', warehouseCode: 'ONT9', account: 'Acc1', appointmentId: 'AP001', appointmentTime: '2024-07-30 10:00:00', earliestAppointmentTime: '2024-07-29 09:00:00', status: 'unused', followUpRecord: '', memo: '', pcNumber: 'PC001', crdd: '2024-07-28 00:00:00', appointmentType: 'floor'
        },
        {
          id: '2', platform: 'Amazon', warehouseCode: 'LAX9', account: 'Acc2', appointmentId: 'AP002', appointmentTime: '2024-07-31 14:30:00', earliestAppointmentTime: '2024-07-30 08:00:00', status: 'used', followUpRecord: '', memo: '', pcNumber: 'PC002', crdd: '2024-07-29 12:00:00', appointmentType: 'pallet'
        },
        {
          id: '3', platform: 'Walmart', warehouseCode: 'WMT-CA1', account: 'Acc3', appointmentId: 'AP003', appointmentTime: '2024-08-01 11:00:00', earliestAppointmentTime: '2024-07-31 10:00:00', status: 'amazon_deleted', followUpRecord: '', memo: '', pcNumber: 'PC003', crdd: '2024-07-30 08:30:00', appointmentType: 'floor'
        },
        {
           id: '4', platform: 'Amazon', warehouseCode: 'ONT8', account: 'Acc1', appointmentId: 'AP004', appointmentTime: '2024-08-02 16:00:00', earliestAppointmentTime: '2024-08-01 14:00:00', status: 'deleted', followUpRecord: '', memo: '', pcNumber: 'PC004', crdd: '2024-07-31 15:00:00', appointmentType: 'pallet'
        }
      ],
      displayData: [],
      loading: false,
      selectedRows: [],
      
      // 
      currentView: 'all',
      customViews: [],
      saveViewDialogVisible: false,
      viewOptionsDialogVisible: false,
      renameViewDialogVisible: false,
      viewForm: {
        name: '',
        columns: []
      },
      currentEditingView: null,
      renameForm: {
        name: ''
      },
      
      // 
      editingCell: {
        rowId: null,
        prop: null,
        value: null
      },
      
      // 
      currentPage: 1,
      pageSize: 10,
      
      // 
      filterPanelVisible: false,
      activeFilters: [],
      filterLogic: 'and',
      
      // 
      groupField: '',
      groupModalVisible: false,
      groupSettings: {
        level1: '',
        level2: '',
        level3: ''
      },
      
      // 
      sortModalVisible: false,
      sortSettings: [{ field: '', direction: 'asc' }],
      
      // 
      columnSettingsVisible: false,
      allColumnsSelected: true,
      columnSettingsList: [],
      
      // 
      contextMenuVisible: false,
      contextMenuX: 0,
      contextMenuY: 0,
      
      // 
      formDialogVisible: false,
      dialogMode: 'add', // 'add' or 'edit'
      currentAppointment: { // 
        id: null,
        platform: '',
        warehouseCode: '',
        account: '',
        appointmentId: '',
        appointmentTime: '',
        earliestAppointmentTime: '',
        status: 'unused', // 
        abnormalStatus: 'normal', // 
        followUpRecord: '',
        memo: '',
        pcNumber: '',
        crdd: '',
        appointmentType: 'floor' // 
      }
    }
  },
  computed: {
    dialogTitle() {
      return this.dialogMode === 'add' ? '' : '';
    },
    
    total() {
      return this.tableData.length;
    },
    
    // 
    tableColumns() {
      return [
        { prop: 'platform', label: '', width: 100, sortable: true, filterType: 'select', 
          filterOptions: [
            { label: 'Amazon', value: 'Amazon' },
            { label: 'Walmart', value: 'Walmart' },
            { label: 'eBay', value: 'eBay' },
            { label: 'Shopify', value: 'Shopify' }
          ]
        },
        { prop: 'warehouseCode', label: '', width: 120, sortable: true },
        { prop: 'account', label: '', width: 120, sortable: true },
        { prop: 'appointmentId', label: '', width: 150, sortable: true },
        { prop: 'pcNumber', label: 'PC', width: 120, sortable: true },
        { prop: 'crdd', label: 'CRDD', width: 160, sortable: true, filterType: 'datetime' },
        { prop: 'appointmentType', label: '', width: 120, sortable: true, filterType: 'select',
          filterOptions: [
            { label: '', value: 'floor' },
            { label: '', value: 'pallet' }
          ]
        },
        { prop: 'appointmentTime', label: '', width: 160, sortable: true, filterType: 'datetime' },
        { prop: 'earliestAppointmentTime', label: '', width: 160, sortable: true, filterType: 'datetime' },
        { prop: 'status', label: '', width: 120, sortable: true, filterType: 'select',
          filterOptions: [
            { label: '', value: 'unused' },
            { label: '', value: 'used' },
            { label: '', value: 'amazon_deleted' },
            { label: '', value: 'deleted' }
          ]
        },
        { prop: 'abnormalStatus', label: '', width: 120, sortable: true, filterType: 'select',
          filterOptions: [
            { label: '', value: 'normal' },
            { label: 'NCNS', value: 'ncns' },
            { label: '', value: 'sold' },
            { label: '', value: 'amazon_deleted' },
            { label: '', value: 'other' }
          ]
        },
        { prop: 'followUpRecord', label: '', minWidth: 150, sortable: false },
        { prop: 'memo', label: 'Memo', minWidth: 150, sortable: false },
        { prop: 'operation', label: '', width: 180, sortable: false, fixed: 'right' }
      ];
    },
    
    // 
    groupableFields() {
      return this.tableColumns.filter(col => col.sortable && col.prop !== 'operation');
    },
    
    // 
    sortableFields() {
      return this.tableColumns.filter(col => col.sortable);
    },
    
    // 
    hasGroupedData() {
      return this.displayData.some(item => item.isGroup);
    }
  },
  methods: {
    // 
    getPlatformType(platform) {
      const platformMap = {
        'Amazon': '',
        'Walmart': 'success',
        'eBay': 'warning',
        'Shopify': 'info'
      };
      return platformMap[platform] || 'info';
    },
    
    // 
    getStatusType(status) {
      const statusMap = {
        unused: 'info',        // 
        used: 'success',      // 
        amazon_deleted: 'danger', // 
        deleted: 'warning'        // 
      };
      return statusMap[status] || 'info';
    },
    
    // 
    getStatusText(status) {
      const statusMap = {
        unused: '',
        used: '',
        amazon_deleted: '',
        deleted: ''
      };
      return statusMap[status] || '';
    },
    
    // 
    getStatusCount(status) {
      return this.tableData.filter(item => item.status === status).length;
    },
    
    // 
    getGroupColumnLabel(fieldName) {
      const field = this.tableColumns.find(col => col.prop === fieldName);
      return field ? field.label : '';
    },
    
    // 
    handleSearch() {
      console.log('', this.searchForm);
      // 
      this.refreshDisplayData();
    },
    
    resetForm() {
      this.searchForm = {
        platform: '',
        warehouseCode: ''
      };
      this.handleSearch();
    },
    
    // 
    refreshDisplayData() {
      this.loading = true;
      // 
      setTimeout(() => {
        // 1. 
        let data = [...this.tableData];
        
        // 
        if (this.currentView !== 'all' && ['unused', 'used', 'amazon_deleted', 'deleted'].includes(this.currentView)) {
          data = data.filter(item => item.status === this.currentView);
        }
        
        // 
        if (this.activeFilters.length > 0) {
          data = this.applyDataFilters(data);
        }
        
        // 2. 
        if (this.groupField) {
          data = this.applyGrouping(data, this.groupField);
        }
        
        // 
        const foundView = this.customViews.find(view => view.id === this.currentView);
        if (foundView && foundView.config) {
          // Todo: 
        }
        
        this.displayData = data;
        this.loading = false;
      }, 300);
    },
    
    // 
    applyDataFilters(data) {
      // 
      if (this.activeFilters.length === 0) return data;
      
      return data.filter(item => {
        const filterResults = this.activeFilters.map(filter => {
          const { field, operator, value } = filter;
          
          // Skip invalid filters
          if (!field || !operator) return true;
          
          const itemValue = item[field];
          
          switch (operator) {
            case 'eq': return itemValue === value;
            case 'neq': return itemValue !== value;
            case 'contains': return String(itemValue).includes(String(value));
            case 'notContains': return !String(itemValue).includes(String(value));
            case 'startsWith': return String(itemValue).startsWith(String(value));
            case 'endsWith': return String(itemValue).endsWith(String(value));
            case 'gt': return itemValue > value;
            case 'gte': return itemValue >= value;
            case 'lt': return itemValue < value;
            case 'lte': return itemValue <= value;
            case 'in': return Array.isArray(value) ? value.includes(itemValue) : false;
            case 'notIn': return Array.isArray(value) ? !value.includes(itemValue) : true;
            default: return true;
          }
        });
        
        return this.filterLogic === 'and'
          ? filterResults.every(Boolean)
          : filterResults.some(Boolean);
      });
    },
    
    // 
    applyGrouping(data, groupField) {
      if (!groupField) return data;
      
      const groupedData = [];
      const groups = {};
      
      // 
      data.forEach(item => {
        const groupValue = item[groupField] || '';
        if (!groups[groupValue]) {
          groups[groupValue] = {
            id: `group_${groupValue}`,
            isGroup: true,
            groupLevel: 1,
            groupName: groupValue,
            groupField: groupField,
            count: 0,
            children: []
          };
          groupedData.push(groups[groupValue]);
        }
        
        groups[groupValue].children.push(item);
        groups[groupValue].count++;
      });
      
      return groupedData;
    },
    
    // 
    handleSelectionChange(selection) {
      this.selectedRows = selection;
    },
    
    handleCellClick(row, column) {
      console.log(':', row, column.property);
    },
    
    handleCellDbClick(row, prop) {
      if (!row.isGroup && ['platform', 'warehouseCode', 'account', 'appointmentId', 'appointmentTime', 'earliestAppointmentTime', 'status', 'followUpRecord', 'memo'].includes(prop)) {
        this.editingCell = {
          rowId: row.id,
          prop: prop,
          value: row[prop]
        };
      }
    },
    
    handleCellEditConfirm(row, prop) {
      if (this.editingCell.rowId === row.id && this.editingCell.prop === prop) {
        // 
        row[prop] = this.editingCell.value;
        
        // 
        this.editingCell = {
          rowId: null,
          prop: null,
          value: null
        };
        
        ElMessage.success('');
      }
    },
    
    // 
    handleAdd() {
      this.dialogMode = 'add';
      this.currentAppointment = {
        id: null,
        platform: '',
        warehouseCode: '',
        account: '',
        appointmentId: '',
        appointmentTime: '',
        earliestAppointmentTime: '',
        status: 'unused',
        abnormalStatus: 'normal',
        followUpRecord: '',
        memo: '',
        pcNumber: '',
        crdd: '',
        appointmentType: 'floor'
      };
      this.formDialogVisible = true;
    },
    
    showLogs() {
      ElMessage.info('');
    },
    
    handleDetail(/* row */) {
      ElMessage.info('');
    },
    
    handleCommand(command) {
      const { action, row } = command;
      if (action === 'edit') {
        this.handleEdit(row);
      } else if (action === 'followUp') {
        this.handleFollowUp(row);
      } else if (action === 'delete') {
        this.handleDelete(row);
      } else if (action === 'createPC') {
        this.handleCreatePC(row);
      }
    },
    
    handleEdit(row) {
      this.dialogMode = 'edit';
      this.currentAppointment = JSON.parse(JSON.stringify(row));
      this.formDialogVisible = true;
    },
    
    handleFollowUp(/* row */) {
      ElMessage.info('');
    },
    
    handleDelete(row) {
      ElMessageBox.confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        const index = this.tableData.findIndex(item => item.id === row.id);
        if (index !== -1) {
          this.tableData.splice(index, 1);
          this.refreshDisplayData();
          ElMessage.success('');
        }
      }).catch(() => {});
    },
    
    handleCreatePC(/* row */) {
      ElMessage.info('PC');
    },
    
    // 
    handleSizeChange(size) {
      this.pageSize = size;
      // 
    },
    
    handleCurrentChange(page) {
      this.currentPage = page;
      // 
    },
    
    // 
    handleCloseDialog() {
      this.formDialogVisible = false;
    },
    
    handleSubmitForm() {
      // 
      if (!this.currentAppointment.platform || !this.currentAppointment.appointmentId) {
        ElMessage.error('');
        return;
      }
      
      // 
      if (this.dialogMode === 'add') {
        const newId = String(Date.now());
        this.tableData.unshift({ ...this.currentAppointment, id: newId });
        ElMessage.success('');
      } else {
        const index = this.tableData.findIndex(item => item.id === this.currentAppointment.id);
        if (index !== -1) {
          this.tableData.splice(index, 1, { ...this.currentAppointment });
          ElMessage.success('');
        }
      }
      
      this.refreshDisplayData();
      this.handleCloseDialog();
    },
    
    // 
    showFilterPanel() {
      this.filterPanelVisible = true;
    },
    
    applyFilters(filters, logic) {
      this.activeFilters = filters;
      this.filterLogic = logic;
      this.refreshDisplayData();
    },
    
    cancelFilters() {
      // 
    },
    
    handleSaveView() {
      this.viewForm = {
        name: '',
        columns: this.tableColumns.map(col => col.prop)
      };
      this.saveViewDialogVisible = true;
    },
    
    saveView() {
      if (!this.viewForm.name) {
        ElMessage.error('');
        return;
      }
      
      const newView = {
        id: `custom_${Date.now()}`,
        name: this.viewForm.name,
        config: {
          columns: this.viewForm.columns,
          filters: [...this.activeFilters],
          filterLogic: this.filterLogic,
          groupField: this.groupField,
          sortSettings: [...this.sortSettings]
        }
      };
      
      this.customViews.push(newView);
      this.saveViewDialogVisible = false;
      this.currentView = newView.id;
      
      ElMessage.success('');
    },
    
    showGroupModal() {
      this.groupSettings = {
        level1: this.groupField || '',
        level2: '',
        level3: ''
      };
      this.groupModalVisible = true;
    },
    
    clearGroupSettings() {
      this.groupSettings = {
        level1: '',
        level2: '',
        level3: ''
      };
    },
    
    applyGroupSettings() {
      this.groupField = this.groupSettings.level1;
      this.groupModalVisible = false;
      this.refreshDisplayData();
    },
    
    showSortModal() {
      this.sortModalVisible = true;
    },
    
    addSortField() {
      this.sortSettings.push({ field: '', direction: 'asc' });
    },
    
    removeSortField(index) {
      this.sortSettings.splice(index, 1);
      if (this.sortSettings.length === 0) {
        this.sortSettings.push({ field: '', direction: 'asc' });
      }
    },
    
    clearSortSettings() {
      this.sortSettings = [{ field: '', direction: 'asc' }];
    },
    
    applySortSettings() {
      // 
      this.sortModalVisible = false;
      this.refreshDisplayData();
    },
    
    showColumnSettings() {
      this.columnSettingsList = JSON.parse(JSON.stringify(this.tableColumns));
      this.allColumnsSelected = this.columnSettingsList.every(col => col.visible !== false);
      this.columnSettingsVisible = true;
    },
    
    handleSelectAllColumns(val) {
      this.columnSettingsList.forEach(col => {
        col.visible = val;
      });
    },
    
    resetColumnSettings() {
      this.columnSettingsList = JSON.parse(JSON.stringify(this.tableColumns));
      this.allColumnsSelected = true;
    },
    
    applyColumnSettings() {
      // 
      this.tableColumns.forEach(col => {
        const foundCol = this.columnSettingsList.find(c => c.prop === col.prop);
        if (foundCol) {
          col.visible = foundCol.visible;
        }
      });
      this.columnSettingsVisible = false;
      ElMessage.success('');
    },
    
    // 
    handleContextMenu(event, view) {
      event.preventDefault();
      this.currentEditingView = view;
      this.contextMenuX = event.clientX;
      this.contextMenuY = event.clientY;
      this.contextMenuVisible = true;
      
      // 
      document.addEventListener('click', this.closeContextMenu, { once: true });
    },
    
    closeContextMenu() {
      this.contextMenuVisible = false;
    },
    
    handleRenameView() {
      if (!this.currentEditingView) return;
      
      this.renameForm.name = this.currentEditingView.name;
      this.viewOptionsDialogVisible = false;
      this.contextMenuVisible = false;
      this.renameViewDialogVisible = true;
    },
    
    saveRenameView() {
      if (!this.renameForm.name) {
        ElMessage.error('');
        return;
      }
      
      const view = this.customViews.find(v => v.id === this.currentEditingView.id);
      if (view) {
        view.name = this.renameForm.name;
        ElMessage.success('');
      }
      
      this.renameViewDialogVisible = false;
    },
    
    handleCopyView() {
      if (!this.currentEditingView) return;
      
      const newView = JSON.parse(JSON.stringify(this.currentEditingView));
      newView.id = `custom_${Date.now()}`;
      newView.name = `${newView.name} ()`;
      
      this.customViews.push(newView);
      this.viewOptionsDialogVisible = false;
      this.contextMenuVisible = false;
      
      ElMessage.success('');
    },
    
    handleDeleteView() {
      if (!this.currentEditingView) return;
      
      ElMessageBox.confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const index = this.customViews.findIndex(v => v.id === this.currentEditingView.id);
        if (index !== -1) {
          this.customViews.splice(index, 1);
          
          if (this.currentView === this.currentEditingView.id) {
            this.currentView = 'all';
          }
          
          ElMessage.success('');
        }
        
        this.viewOptionsDialogVisible = false;
        this.contextMenuVisible = false;
      }).catch(() => {});
    },
    
    // 
    getAbnormalStatusType(status) {
      const statusMap = {
        normal: 'success',    // 
        ncns: 'danger',      // NCNS
        sold: 'warning',     // 
        amazon_deleted: 'danger', // 
        other: 'info'        // 
      };
      return statusMap[status] || 'info';
    },
    
    getAbnormalStatusText(status) {
      const statusMap = {
        normal: '',
        ncns: 'NCNS',
        sold: '',
        amazon_deleted: '',
        other: ''
      };
      return statusMap[status] || '';
    }
  },
  created() {
    // 
    this.displayData = [...this.tableData];
  },
  mounted() {
    // 
    this.refreshDisplayData();
  },
  watch: {
    currentView() {
      this.refreshDisplayData();
    }
  }
}
</script>

<style scoped>
