}

.group-level-3 {
  background-color: #fdf6ec;
}

/*  */
.column-settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.column-item {
  display: flex;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ebeef5;
}

.drag-handle {
  cursor: move;
  margin-right: 10px;
  color: #909399;
}

/*  */
.el-form-item .el-date-editor.el-input,
.el-form-item .el-date-editor.el-input__inner {
  width: 100%;
}
</style>
