<template>
    <el-form ref="tagFormRef" :model="formData" :rules="formRules" label-width="120px" class="tag-form">
      <el-form-item label=" Key" prop="tagKey">
        <el-input
          v-model="formData.tagKey"
          placeholder=": category.domain.name (e.g., business.order.userId)"
          :disabled="mode === 'edit'"
        ></el-input>
        <div class="form-tip">(.)</div>
      </el-form-item>
  
      <el-form-item label="/" prop="description">
        <el-input v-model="formData.description" type="textarea" placeholder=""></el-input>
      </el-form-item>
  
      <el-form-item label="" prop="category">
        <el-select v-model="formData.category" placeholder="">
          <el-option label=" (Business)" value="Business"></el-option>
          <el-option label=" (Warning)" value="Warning"></el-option>
          <el-option label=" (Anomaly)" value="Anomaly"></el-option>
        </el-select>
      </el-form-item>
  
      <el-form-item label="" prop="valueType">
        <el-select v-model="formData.valueType" placeholder="" @change="handleValueTypeChange">
          <el-option label=" (String)" value="String"></el-option>
          <el-option label=" (Number)" value="Number"></el-option>
          <el-option label=" (Boolean)" value="Boolean"></el-option>
          <el-option label=" (Enum)" value="Enum"></el-option>
        </el-select>
      </el-form-item>
  
      <!--  Enum  -->
      <el-form-item v-if="formData.valueType === 'Enum'" label="" prop="allowedValues">
         <div v-for="(item, index) in formData.allowedValues" :key="index" class="enum-item">
           <el-input v-model="item.key" placeholder=" Key (/)" style="width: 40%; margin-right: 10px;"></el-input>
           <el-input v-model="item.value" placeholder=" Value ()" style="width: 40%; margin-right: 10px;"></el-input>
           <el-button type="danger" :icon="Delete" circle @click="removeAllowedValue(index)" />
         </div>
         <el-button type="primary" plain @click="addAllowedValue"></el-button>
         <div class="form-tip"> Key-Value Key </div>
      </el-form-item>
  
       <el-form-item label="/" prop="owner">
        <el-input v-model="formData.owner" placeholder=""></el-input>
      </el-form-item>
  
      <el-form-item label="" prop="notes">
        <el-input v-model="formData.notes" type="textarea" placeholder=""></el-input>
      </el-form-item>
  
    </el-form>
  </template>
  
  <script>
  import { ref, reactive, watch, onMounted } from 'vue';
  import { ElMessage } from 'element-plus';
  import { Delete } from '@element-plus/icons-vue'; // 
  
  //  Tag Key 
  const checkTagKeyUnique = (rule, value, callback) => {
    console.log('Checking uniqueness for:', value);
    //  API 
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        //  business.order.userId 
        if (value === 'business.order.userId' /* && key */) {
           reject(new Error(' Key '));
        } else {
           resolve();
        }
      }, 300); // 
     }).then(() => callback()).catch(error => callback(error));
  };
  
  //  Tag Key 
  const validateTagKeyFormat = (rule, value, callback) => {
    const pattern = /^[a-z0-9]+(\.[a-z0-9]+)*$/;
    if (!value) {
        return callback(new Error(' Key '));
    }
    if (!pattern.test(value)) {
      return callback(new Error(' (: category.domain.name, //)'));
    }
    return callback();
  };
  
  // 
  const validateAllowedValues = (rule, value, callback) => {
      if (!value || value.length === 0) {
          return callback(new Error(''));
      }
      for (const item of value) {
          if (!item.key || !item.value) {
              return callback(new Error(' Key  Value '));
          }
          //  Key 
      }
      callback();
  };
  
  
  export default {
    name: 'TagForm',
    props: {
      initialData: {
        type: Object,
        default: null
      },
      mode: {
        type: String, // 'add' or 'edit'
        default: 'add'
      }
    },
    emits: ['save-success'], // 
    setup(props, { emit }) {
      const tagFormRef = ref(null);
      const formData = reactive({
        tagKey: '',
        description: '',
        category: 'Business', // 
        valueType: 'String', // 
        allowedValues: [], //  Enum 
        owner: '',
        notes: '',
        // ---  ---
        id: null,
        status: 'Active',
        createdAt: '',
        updatedAt: '',
        creator: 'CurrentUser' // 
      });
  
      const formRules = reactive({
        tagKey: [
          { required: true, validator: validateTagKeyFormat, trigger: 'blur' },
          //  Key  Key 
          // { validator: checkTagKeyUnique, trigger: 'blur', isEdit: props.mode === 'edit' } // 
           { validator: (rule, value, callback) => {
               if (props.mode === 'add') { // 
                  return checkTagKeyUnique(rule, value, callback);
               }
               callback(); // 
             }, trigger: 'blur'
           }
        ],
        description: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        category: [
          { required: true, message: '', trigger: 'change' }
        ],
        valueType: [
          { required: true, message: '', trigger: 'change' }
        ],
        allowedValues: [
           //  valueType  Enum 
           { validator: (rule, value, callback) => {
               if (formData.valueType === 'Enum') {
                   return validateAllowedValues(rule, value, callback);
               }
               callback(); //  Enum 
             }, trigger: 'change' // /
           }
        ]
      });
  
      // --- Methods ---
      const resetForm = () => {
        tagFormRef.value?.resetFields(); // 
        //  resetFields 
        formData.tagKey = '';
        formData.description = '';
        formData.category = 'Business';
        formData.valueType = 'String';
        formData.allowedValues = [];
        formData.owner = '';
        formData.notes = '';
        formData.id = null;
        formData.status = 'Active';
      };
  
      const setForm = (data) => {
          if (!data) return;
          resetForm(); // 
          formData.id = data.id;
          formData.tagKey = data.tagKey;
          formData.description = data.description;
          formData.category = data.category;
          formData.valueType = data.valueType;
          // 
          formData.allowedValues = data.allowedValues ? JSON.parse(JSON.stringify(data.allowedValues)) : [];
          formData.owner = data.owner || '';
          formData.notes = data.notes || '';
          formData.status = data.status;
          formData.createdAt = data.createdAt;
          formData.updatedAt = data.updatedAt;
          formData.creator = data.creator;
      };
  
      const addAllowedValue = () => {
        formData.allowedValues.push({ key: '', value: '' });
      };
  
      const removeAllowedValue = (index) => {
        formData.allowedValues.splice(index, 1);
      };
  
      const handleValueTypeChange = (newType) => {
          if (newType !== 'Enum') {
              formData.allowedValues = []; //  Enum 
          } else if (formData.allowedValues.length === 0) {
              addAllowedValue(); // Enum 
          }
           //  allowedValues 
          tagFormRef.value?.clearValidate('allowedValues');
      };
  
      const getFormData = () => {
         // 
         const dataToSubmit = {
           tagKey: formData.tagKey,
           description: formData.description,
           category: formData.category,
           valueType: formData.valueType,
           owner: formData.owner,
           notes: formData.notes,
         };
         if (formData.valueType === 'Enum') {
           dataToSubmit.allowedValues = formData.allowedValues.filter(item => item.key && item.value); // 
         }
         if (props.mode === 'edit' && formData.id) {
           dataToSubmit.id = formData.id; //  ID
         }
         // 
         return dataToSubmit;
      };
  
      const validate = () => {
         return tagFormRef.value?.validate();
      };
  
      const submitForm = async () => {
        try {
          await validate();
          const dataToSave = getFormData();
          console.log('Form validation passed. Data to save:', dataToSave);
  
          // ---  API  ---
          console.log(`Simulating API call to ${props.mode === 'add' ? 'create' : 'update'} tag...`);
          
          // API
          await new Promise(resolve => setTimeout(resolve, 500));

          // 
          const savedData = {
            ...dataToSave,
            id: props.mode === 'add' ? Date.now() : formData.id, // ID
            status: formData.status || 'Active',
            creator: formData.creator || 'CurrentUser',
            createdAt: formData.createdAt || new Date().toISOString(),
            updatedAt: new Date().toISOString()
          };

          // 
          emit('save-success', savedData);
          ElMessage.success(`${props.mode === 'add' ? '' : ''}`);
          
          if (props.mode === 'add') {
            resetForm(); // 
          }
        } catch (error) {
          console.error('Form validation or submission failed:', error);
          ElMessage.error(error.message || `${props.mode === 'add' ? '' : ''}`);
        }
      };
  
  
      // --- Watchers ---
      watch(() => props.initialData, (newVal) => {
        if (newVal) {
          setForm(newVal);
        }
      }, { immediate: true });
  
      //  Enum 
      onMounted(() => {
          if (formData.valueType === 'Enum' && formData.allowedValues.length === 0) {
              addAllowedValue();
          }
      });
  
      // --- Expose methods to parent ---
      // (Vue 3 setup  expose  defineExpose)
      //  ref.$refs.tagFormRef.method()  expose
  
      return {
        tagFormRef,
        formData,
        formRules,
        resetForm,
        submitForm,
        addAllowedValue,
        removeAllowedValue,
        handleValueTypeChange,
        getFormData, // 
        validate,    // 
        Delete // 
      };
    }
  };
  </script>
  
  <style scoped>
  .tag-form {
    padding: 20px;
  }
  
  .form-tip {
    font-size: 12px;
    color: #909399;
    margin-top: 4px;
    line-height: 1.4;
  }
  
  .enum-item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .enum-item:last-child {
    margin-bottom: 15px;
  }
  </style>